"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { useToast } from "@/components/ui/use-toast"
import usuarioService, { type Usuario, type NovoUsuario, type EditarUsuario } from "@/services/usuarioService"

interface UsuarioFormProps {
  usuario?: Usuario
  onSuccess?: () => void
  onCancel?: () => void
}

export function UsuarioForm({ usuario, onSuccess, onCancel }: UsuarioFormProps) {
  const { toast } = useToast()
  const [loading, setLoading] = useState(false)
  const [formData, setFormData] = useState<NovoUsuario | EditarUsuario>({
    nome: "",
    username: "",
    email: "",
    senha: "",
    cargo: "",
    perfil: "Operador",
    status: "Ativo",
  })

  // Preencher o formulário com os dados do usuário, se fornecido
  useEffect(() => {
    if (usuario) {
      setFormData({
        nome: usuario.nome || "",
        username: usuario.username || "",
        email: usuario.email || "",
        senha: "", // Não preencher a senha por segurança
        cargo: usuario.cargo || "",
        perfil: usuario.perfil || "Operador",
        status: usuario.status || "Ativo",
      })
    }
  }, [usuario])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setFormData((prev) => ({ ...prev, [name]: value }))
  }

  const handleSelectChange = (name: string, value: string) => {
    setFormData((prev) => ({ ...prev, [name]: value }))
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)

    try {
      // Verificar se é uma edição ou criação
      if (usuario) {
        // Se for edição, verificar se a senha foi fornecida
        const dadosAtualizacao: EditarUsuario = { ...formData }

        // Se a senha estiver vazia, não enviar para o servidor
        if (!dadosAtualizacao.senha) {
          delete dadosAtualizacao.senha
        }

        await usuarioService.atualizarUsuario(usuario.id, dadosAtualizacao)
        toast({
          title: "Sucesso",
          description: "Usuário atualizado com sucesso.",
        })
      } else {
        // Se for criação, verificar se todos os campos obrigatórios foram preenchidos
        if (!formData.nome || !formData.username || !formData.senha || !formData.cargo) {
          throw new Error("Preencha todos os campos obrigatórios.")
        }

        // Verificar se o username já existe antes de enviar
        const usernameDisponivel = await usuarioService.verificarUsernameDisponivel(formData.username)
        if (!usernameDisponivel) {
          throw new Error(`O nome de usuário '${formData.username}' já está em uso. Escolha outro nome de usuário.`)
        }

        await usuarioService.criarUsuario(formData as NovoUsuario)
        toast({
          title: "Sucesso",
          description: "Usuário criado com sucesso.",
        })
      }

      // Chamar o callback de sucesso, se fornecido
      if (onSuccess) {
        onSuccess()
      }
    } catch (error: any) {
      console.error("Erro ao salvar usuário:", error)
      toast({
        title: "Erro",
        description: error.message || "Não foi possível salvar o usuário.",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="space-y-2">
        <Label htmlFor="nome">Nome</Label>
        <Input id="nome" name="nome" value={formData.nome} onChange={handleChange} required />
      </div>

      <div className="space-y-2">
        <Label htmlFor="username">Nome de Usuário</Label>
        <Input
          id="username"
          name="username"
          value={formData.username}
          onChange={handleChange}
          required
          disabled={!!usuario} // Desabilitar edição de username para usuários existentes
        />
        {!!usuario && (
          <p className="text-xs text-muted-foreground">O nome de usuário não pode ser alterado após a criação.</p>
        )}
      </div>

      <div className="space-y-2">
        <Label htmlFor="email">Email</Label>
        <Input id="email" name="email" type="email" value={formData.email} onChange={handleChange} />
      </div>

      <div className="space-y-2">
        <Label htmlFor="senha">Senha {usuario ? "(deixe em branco para manter a senha atual)" : ""}</Label>
        <Input
          id="senha"
          name="senha"
          type="password"
          value={formData.senha}
          onChange={handleChange}
          required={!usuario} // Senha é obrigatória apenas para novos usuários
        />
      </div>

      <div className="space-y-2">
        <Label htmlFor="cargo">Cargo</Label>
        <Input id="cargo" name="cargo" value={formData.cargo} onChange={handleChange} required />
      </div>

      <div className="space-y-2">
        <Label htmlFor="perfil">Perfil</Label>
        <Select value={formData.perfil} onValueChange={(value) => handleSelectChange("perfil", value)}>
          <SelectTrigger>
            <SelectValue placeholder="Selecione um perfil" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="Administrador">Administrador</SelectItem>
            <SelectItem value="Operador">Operador</SelectItem>
          </SelectContent>
        </Select>
      </div>

      <div className="space-y-2">
        <Label htmlFor="status">Status</Label>
        <Select value={formData.status} onValueChange={(value) => handleSelectChange("status", value)}>
          <SelectTrigger>
            <SelectValue placeholder="Selecione um status" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="Ativo">Ativo</SelectItem>
            <SelectItem value="Inativo">Inativo</SelectItem>
          </SelectContent>
        </Select>
      </div>

      <div className="flex justify-end gap-2 pt-4">
        {onCancel && (
          <Button type="button" variant="outline" onClick={onCancel}>
            Cancelar
          </Button>
        )}
        <Button type="submit" disabled={loading}>
          {loading ? "Salvando..." : usuario ? "Atualizar" : "Criar"}
        </Button>
      </div>
    </form>
  )
}
